import {
  Streamlit,
  StreamlitComponentBase,
  withStreamlitConnection,
} from "streamlit-component-lib"
import React, { ReactNode } from "react";
import Card from '@mui/joy/Card';
import CardContent from '@mui/joy/CardContent';
import CardCover from '@mui/joy/CardCover';
import CardMedia from '@mui/material/CardMedia';
import Typography from '@mui/joy/Typography';
import background from './images/background.png';
import fd_symbol from './images/fd_symbol.png';

import "@fontsource/alfa-slab-one";

class CardComponent extends StreamlitComponentBase<any> {
  public render = (): ReactNode => {
    const question = this.props.args["question"]

    return (
      <div style={{padding: "0.5rem"}}>
        <Card variant="outlined" sx={{width: 400, height:325, border: '.25px solid rgba(100, 100, 100)', "--Card-radius": "20px", mx: 'auto'}}>
          <CardCover>
            <CardMedia image={background}/>
          </CardCover>
          <CardContent>
            <CardMedia image={fd_symbol} sx={{width: 225, height:125, mx:"auto", mt: 2, border: '.25px solid rgba(100, 100, 100)'}}/>
            <Typography level="h3" sx={{mt: 6, color:"#000",textAlign:"center"}} style={{fontFamily: "Alfa Slab One"}}>
              {question}
            </Typography>
          </CardContent>
        </Card>
      </div> 
    )
  }
}

export default withStreamlitConnection(CardComponent)